import React, { Component, Fragment } from "react";
import funcoesApiWooCommerce from "../../util/ApiWooCommerce";
import MostraProdutos from "../MostraProdutos";
import CircleLoading from "../loading/CircleLoading";

class EscolherAlizar extends Component {
   constructor(props) {
      super(props);

      this.state = {
         categoriaSlug: this.props.categoriaSlug,
         produtos: null,
         produtosMostraveis: null,
      };
   }

   componentDidMount() {
      this.chamaApiParaRceberProdutos(this.state.categoriaSlug);
   }

   // componentWillReceiveProps(nextProps) {
   //    const id = nextProps.match.params.id;
   //    this.chamaApiParaRceberProdutos(id);
   // }

   // chamaApiParaRceberProdutos(id) {
   //    ApiWooCommerce.getCategoriaPublishProducts(id).then((res) => {
   //       this.setState({ produtos: res.data, paginaId: id });
   //    });
   // }

   async componentWillReceiveProps(nextProps) {
      console.log(nextProps.categoriaSlug);
      this.setState({ categoriaSlug: nextProps.categoriaSlug });
      this.attProdutosMostraveis(nextProps.categoriaSlug);
      console.log(this.state.produtosMostraveis);
   }

   attProdutosMostraveis(categoriaSlug) {
      const p = this.state.produtos.filter((prod) => {
         let mostra = false;

         prod.categories.forEach((element) => {
            let bool = false;

            if (!bool) {
               bool = element.slug === categoriaSlug;
               if (bool) mostra = true;
            } else {
               mostra = true;
            }
         });

         return mostra;
      });

      this.setState({
         produtosMostraveis: p,
      });
   }

   async chamaApiParaRceberProdutos(categoriaSlug) {
      await funcoesApiWooCommerce.getAllPublishedProducts().then((res) => {
         this.setState({
            produtos: res.data,
         });
         this.attProdutosMostraveis(categoriaSlug);
      });
   }

   render() {
      return (
         <Fragment>
            {this.state.produtosMostraveis ? (
               <MostraProdutos produtos={this.state.produtosMostraveis} />
            ) : (
               <center>
                  <CircleLoading />
               </center>
            )}
         </Fragment>
      );
   }
}

export default EscolherAlizar;
